import React from "react";
import Popup from "reactjs-popup";
import classnames from "classnames";
//
import QuestionIcon from "../assets/svg/Question";

function HelpPopup({
  children,
  placeholder,
  withBorder = false,
  className,
  position = "left center"
}) {
  return (
    <Popup
      trigger={
        <span
          className={classnames("cursor-pointer", {
            "text-primary": !withBorder,
            "text-white border-info bg-info rounded px-2": withBorder,
            [className]: className
          })}
        >
          {placeholder}
          <QuestionIcon
            className="font-svg down-1 mr-2"
            fill="currentColor"
            fillRule="evenodd"
          />
        </span>
      }
      position={position}
      on="hover"
      contentStyle={{
        border: "1px solid #e6e6e6",
        borderRadius: "2px",
        minWidth: "6em",
        width: "300px",
        maxWidth: "100%",
        boxShadow: "0 0 5px -2px rgba(69, 69, 69, 0.5)",
        padding: "10px 15px"
      }}
    >
      <small className="text-muted text-pre-line">{children}</small>
    </Popup>
  );
}

export default HelpPopup;
