import React, { useState } from "react";
import { connect } from "react-redux";
import makeComponentTrashable from "trashable-react";
import DatePicker from "react-persian-calendar-date-picker";
import isEmpty from "lodash/isEmpty";
import { css } from "glamor";
import {
  reduxForm,
  Field,
  startAsyncValidation,
  stopAsyncValidation
} from "redux-form";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import {
  TextField,
  TextareaField,
  SelectField
} from "../../../../../components/Fields";

function CreateApplicationInfo(props) {
  const { asyncValidating, pristine } = props;

  const [selectedDay, setSelectedDay] = useState(null);

  return (
    <div>
      <form
        onSubmit={props.handleSubmit(props.onSubmit)}
        className="mt-4"
        noValidate
      >
        <div className="form-row">
          <Field
            name="icons"
            labelText="نوع آیکون"
            type="select"
            rtl
            placeholder="لطفا نوع آیکون را انتخاب کنید"
            containerClass="form-group col-md-6"
            options={[
              { value: "fontawsome", label: "Fontawsome" },
              { value: "glyphicon", label: "Glyphicon" }
            ]}
            component={SelectField}
          />
          <Field
            name="title"
            type="text"
            containerClass="form-group col-md-6"
            labelText="عنوان"
            placeholder="عنوان را وارد کنید"
            className="form-control"
            component={TextField}
            disabled={asyncValidating}
          />
          <Field
            name="link"
            type="text"
            containerClass="form-group col-md-6"
            labelText="لینک عنوان"
            placeholder="لینک را وارد کنید"
            className="form-control"
            component={TextField}
            disabled={asyncValidating}
          />
          <Field
            name="categories"
            labelText="دسته بندی"
            type="select"
            rtl
            placeholder="لطفا دسته بندی را انتخاب کنید"
            containerClass="form-group col-md-6"
            options={[
              { value: "fontawsome", label: "Fontawsome" },
              { value: "glyphicon", label: "Glyphicon" }
            ]}
            component={SelectField}
          />
          <Field
            name="cas"
            labelText="نوع دسترسی"
            type="select"
            rtl
            placeholder="لطفا نوع دسترسی را انتخاب کنید"
            containerClass="form-group col-md-6"
            options={[
              { value: "fontawsome", label: "Fontawsome" },
              { value: "glyphicon", label: "Glyphicon" }
            ]}
            component={SelectField}
          />
          <div
            className="form-group col-6"
            {...css({
              position: "relative",
              zIndex: "1000",
              " .DatePicker": {
                width: "100%",
                " .DatePicker__input": {
                  height: "35px",
                  width: "100%",
                  padding: ".375rem .75rem",
                  cursor: "pointer"
                }
              }
            })}
          >
            <label>انتخاب تاریخ</label>
            <DatePicker
              selectedDay={selectedDay}
              onChange={setSelectedDay}
              inputPlaceholder="انتخاب روز"
            />
          </div>
          <Field
            labelText="توضیحات"
            type="text"
            name="description"
            containerClass="form-group col-12"
            rows="12"
            className="form-control"
            component={TextareaField}
            disabled={asyncValidating}
          />
        </div>

        <div className="d-flex justify-content-between">
          <button
            type="submit"
            className="btn btn-primary"
            disabled={asyncValidating || pristine}
          >
            <FontAwesomeIcon
              icon="step-forward"
              className="ml-2"
              transform="down-2"
            />
            ذخیره
          </button>
          <button onClick={props.onClose} className="btn btn-danger">
            بستن
            <FontAwesomeIcon
              icon={["far", "times-circle"]}
              className="mr-2"
              transform="down-2"
            />
          </button>
        </div>
      </form>
    </div>
  );
}

const validate = values => {
  const names = [
    "name",
    "developer",
    "description",
    "keywords",
    "price",
    "categories",
    "subCategories"
  ];
  const errors = {};

  for (let i = 0; i < names.length; i++) {
    if (values[names[i]] === undefined) {
      errors[names[i]] = "این فیلد نباید خالی باشد";
    }
  }

  if (isEmpty(values.keywords)) errors.keywords = "این فیلد نباید خالی باشد";

  return errors;
};

const mapStateToProps = ({
  auth,
  modal: { initialValues },
  form: { createApplication: { values = {} } = {} }
}) => ({ auth, initialValues, formValues: values });

export default connect(mapStateToProps)(
  reduxForm({
    form: "createApplication",
    destroyOnUnmount: false, // <------ preserve form data
    forceUnregisterOnUnmount: true, // <------ unregister fields on unmount
    validate,
    enableReinitialize: true
  })(makeComponentTrashable(CreateApplicationInfo))
);
