import React, { Component } from "react";
import classnames from "classnames";
import isEmpty from "lodash/isEmpty";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { css } from "glamor";

const rules = {
  ltr: css({
    right: "0.75rem",
    left: "unset",
    color: "red"
  }),
  rtl: css({
    right: "unset",
    left: "0.75rem",
    color: "red"
  }),
  svg: css({
    position: "absolute",
    color: "#c80000",
    top: "50%",
    cursor: "help",
    WebkitTransform: "translateY(-50%)",
    MozTransform: "translateY(-50%)",
    msTransform: "translateY(-50%)",
    OTransform: "translateY(-50%)",
    transform: "translateY(-50%)"
  })
};

class TextareaField extends Component {
  constructor(props) {
    super(props);
    this.handleError = this.handleError.bind(this);
  }

  handleError() {
    const {
      meta: { error },
      ltr
    } = this.props;
    if (!isEmpty(error)) {
      return (
        <FontAwesomeIcon
          icon="exclamation-circle"
          size="xs"
          className={classnames(`${rules.svg}`, {
            [`${rules.ltr}`]: ltr,
            [`${rules.rtl}`]: !ltr
          })}
        />
      );
    }
  }

  render() {
    const {
      input,
      labelText,
      containerClass,
      ltr,
      smallText,
      meta: { touched, error },
      ...props
    } = this.props;

    return (
      <div className={containerClass}>
        {labelText && <label htmlFor={input.name}>{labelText}</label>}
        <div
          className={classnames("wrap-input position-relative", {
            "alert-validate": touched && error,
            ltr
          })}
          data-validate={error}
        >
          <textarea
            name={input.name}
            {...props}
            {...input}
            className={classnames(`${props.className}`, {
              invalid: touched && !isEmpty(error)
            })}
          />
          {touched && this.handleError()}
        </div>
        {smallText && (
          <small className="text-muted float-right mt-1">{smallText}</small>
        )}
      </div>
    );
  }
}
export default TextareaField;
