import React, { Component } from "react";
import { connect } from "react-redux";
import Transition from "react-transition-group/Transition";
import EditBookmark from "./EditBookmark";

import { hideModal } from "../services/actions";

const transitionWrapper = (children, inProp) => (
  <Transition
    in={inProp}
    mountOnEnter
    unmountOnExit
    timeout={{
      enter: 0,
      exit: 350
    }}
  >
    {children}
  </Transition>
);
class ModalContainer extends Component {
  render() {
    const {
      hideModal,
      modal: { visible, onProcessDone }
    } = this.props;

    return (
      <div>
        {visible && <style>{"html,body {overflow-y:hidden}"}</style>}

        {transitionWrapper(
          <EditBookmark
            visible={visible === "editBookmark"}
            onClose={hideModal}
            onProcessDone={onProcessDone}
          />,
          visible === "editBookmark"
        )}
      </div>
    );
  }
}

function mapStateToProps({ modal }) {
  return { modal };
}
export default connect(
  mapStateToProps,
  { hideModal }
)(ModalContainer);
