import { SET_MODAL_PARAMS, HIDE_MODAL, SET_INITIAL_VALUES } from "./actions";

const initialState = {
  props: null,
  visible: null,
  onProcessDone: null
};
export default (state = initialState, action) => {
  switch (action.type) {
    case SET_MODAL_PARAMS:
      return {
        props: action.props,
        visible: action.target,
        onProcessDone: action.onProcessDone
      };

    case HIDE_MODAL:
      return { ...state, visible: initialState.visible };

    case SET_INITIAL_VALUES:
      return { ...state, initialValues: action.payload };

    default:
      return state;
  }
};
