import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classnames from "classnames";
import { connect } from "react-redux";
import Popup from "reactjs-popup";
import { css } from "glamor";
import { tabs } from "../services/mock";

import { setModalParams } from "../../../containers/Modal";

const rules = {
  dropdown: css({
    fontSize: "1rem",
    " .item": {
      cursor: "pointer",
      width: "100%",
      ":hover": {
        backgroundColor: "#fafafa"
      }
    }
  })
};

const renderLockIcon = function renderLockIcon({ cas, lock }) {
  if (cas && lock) return <FontAwesomeIcon icon="lock" />;
  else return null;
};

function LandingResults({ setModalParams }) {
  const renderBookmarks = function renderBookmarks(bookmarks) {
    return bookmarks.map(({ red, title, icon, cas, lock }) => {
      return (
        <div className="col-4 d-flex justify-content-between mb-1">
          <div className="d-flex w-100">
            <Popup
              trigger={
                <span
                  style={{ cursor: "pointer" }}
                  className={classnames(
                    "ml-2 p-1 text-white rounded d-flex align-items-center",
                    {
                      "bg-primary": !red,
                      "bg-danger": red
                    }
                  )}
                  onClick={() => {
                    alert("slm");
                  }}
                >
                  <FontAwesomeIcon icon="chevron-down" size="xs" />
                  <FontAwesomeIcon
                    className="mr-1"
                    icon={icon.substr(icon.indexOf("-") + 1)}
                    style={{ width: "25px" }}
                  />
                </span>
              }
              position="bottom center"
              contentStyle={{
                padding: "0px",
                border: "none",
                borderRadius: "5px",
                width: "100px"
              }}
            >
              <div className={rules.dropdown}>
                <div className="item p-2 d-flex justify-content-between align-items-center">
                  حذف <FontAwesomeIcon icon="trash" size="xs" />
                </div>
                <div
                  className="item p-2 d-flex justify-content-between align-items-center"
                  onClick={() => {
                    setModalParams({ target: "editBookmark" });
                  }}
                >
                  ویرایش <FontAwesomeIcon icon="edit" size="xs" />
                </div>
              </div>
            </Popup>

            <h3
              className={classnames("w-85 d-block text-nowrap text-truncate", {
                "text-danger": red
              })}
            >
              {title}
            </h3>
          </div>
          <div>{renderLockIcon({ cas, lock })}</div>
        </div>
      );
    });
  };

  return (
    <div className="container">
      {tabs.map(tab => {
        return (
          <section>
            <h2 className="divider-bottom p-2">{tab.text}</h2>
            <div className="row pt-1 pb-3">
              {renderBookmarks(tab.bookmarks)}
            </div>
          </section>
        );
      })}
    </div>
  );
}

export default connect(
  null,
  { setModalParams }
)(LandingResults);
