import React from "react";

function LandingSearchBar() {
  return (
    <div
      className="d-block bg-primary p-2 d-flex align-items-center justify-content-center"
      style={{ minHeight: " 25vh" }}
    >
      <div className="container">
        <form className="curve">
          <input
            type="text"
            className="d-inline-block form-control 2-75"
            placeholder="برای جستجو کلیک کنید..."
          />
        </form>
      </div>
    </div>
  );
}

export default LandingSearchBar;
