import React from "react";
import { connect } from "react-redux";
import Rodal from "rodal";

import Information from "./components/Information";

const modalStyle = {
  dialog: {
    width: "700px",
    borderRadius: "5px",
    maxWidth: "100%"
  }
};

function ModalEditBookmark(props) {
  const { visible, onClose, afterOnClose } = props;
  const onSubmit = function onSubmit(values) {};
  console.log({ visible, onClose });
  return (
    <Rodal
      customStyles={modalStyle.dialog}
      visible={true}
      onClose={() => {
        onClose();
        if (afterOnClose) afterOnClose();
      }}
      animation="rotate"
      // showCloseButton={false}
      duration={350}
    >
      <Information onSubmit={onSubmit} {...props} />
    </Rodal>
  );
}

const mapStateToProps = ({ modal: { props } }) => props;

export default connect(mapStateToProps)(ModalEditBookmark);
