import React, { Component } from "react";
import classnames from "classnames";
import isEmpty from "lodash/isEmpty";
import { css } from "glamor";

import HelpPopup from "../../HelpPopup";
import DangerIcon from "../../../assets/svg/Danger";

const rules = {
  ltr: css({
    right: "0.75rem",
    left: "unset",
    color: "red"
  }),
  rtl: css({
    right: "unset",
    left: "0.75rem",
    color: "red"
  }),
  svg: css({
    position: "absolute",
    color: "#c80000",
    top: "50%",
    cursor: "help",
    transform: "translateY(-50%)"
  })
};

class TextField extends Component {
  constructor(props) {
    super(props);
    this.state = {
      alertIsVisible: true
    };
    this.handleVisibility = this.handleVisibility.bind(this);
    this.handleError = this.handleError.bind(this);
  }

  handleError() {
    const {
      meta: { error },
      ltr
    } = this.props;
    if (!isEmpty(error)) {
      return (
        <DangerIcon
          className={classnames(`font-svg xs ${rules.svg}`, {
            [`${rules.ltr}`]: ltr,
            [`${rules.rtl}`]: !ltr
          })}
          fill="currentColor"
          fillRule="evenodd"
        />
      );
    }
  }

  handleVisibility() {
    this.setState(prevState => ({
      alertIsVisible: !prevState.alertIsVisible
    }));
  }

  render() {
    const {
      input,
      type,
      labelText,
      helpText,
      className,
      containerClass,
      placeholder,
      isOptional = false,
      ltr,
      smallText,
      meta: { touched, error },
      extraChildren,
      ...props
    } = this.props;
    const { alertIsVisible } = this.state;

    return (
      <div className={containerClass}>
        {labelText && (
          <label htmlFor={input.name}>
            <React.Fragment>
              <span>{labelText}</span>
              {isOptional && (
                <span className="mr-2 border text-white bg-warning border-warning rounded px-1 h4">
                  اختیاری
                </span>
              )}
            </React.Fragment>
          </label>
        )}
        {helpText && <HelpPopup>{helpText}</HelpPopup>}
        <div
          className={classnames("wrap-input position-relative", {
            "alert-validate": alertIsVisible && touched && error,
            ltr
          })}
          data-validate={error}
        >
          <input
            {...props}
            {...input}
            type={type}
            placeholder={placeholder}
            className={classnames(className, {
              invalid: touched && !isEmpty(error)
            })}
            onFocus={e => {
              input.onFocus(e);
              this.handleVisibility();
            }}
            onBlur={e => {
              input.onBlur(e);
              this.handleVisibility();
            }}
            disabled={this.props.disabled}
          />
          {touched && this.handleError()}
        </div>
        {smallText && <small className="text-muted">{smallText}</small>}
        {extraChildren && extraChildren()}
      </div>
    );
  }
}
export default TextField;
