import axios from "axios";
export const SET_MODAL_PARAMS = "set_modal_params";
export const HIDE_MODAL = "hide_modal";
export const SET_INITIAL_VALUES = "get_app_to_update";

export const setModalParams = params => ({
  type: SET_MODAL_PARAMS,
  ...params
});

export const initializeBookmark = (appId, _linkId) => async dispatch => {
  const res = await axios.get(
    `${process.env.REACT_APP_API_URL}/applications/${appId}/links/${_linkId}`
  );

  const payload = res.data;

  return dispatch({ type: SET_INITIAL_VALUES, payload });
};

export const hideModal = () => ({ type: HIDE_MODAL });
